.app {
  height: 100%;
  padding: 0 100px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.content {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 100px 100px;
  text-align: center;
}

@media (max-width: 767px) {
  .content {
    padding: 50px 0px;
  }

  .app {
    padding: 0 10px;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .content {
    padding: 50px 0px;
  }
}

@media (min-width: 992px) and (max-width: 1599px) {
  .content {
    padding: 50px 200px;
  }
}

@media (min-width: 1600px) and (max-width: 2299px) {
  .content {
    padding: 50px 300px;
  }
}



